import html from '../libs/snabby/snabby.js';

let fn = () => alert(777);

// Create vnodes:
let foo = html`<div>
    Hello Earth
    <a @props=${{ href: '/foo', textContent: 'Hello' }}></a>
    <div @on:click=${fn}>777</div>
</div>`;
let bar = html`<div @on:click=${fn}>777</div>`;

// Patch to DOM:
html.update(document.body, foo)

